import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, ParamMap,Router} from "@angular/router";
import { ApiserviceService } from '../apiservice.service'
import { Carts } from '../carts';
import { Books } from '../books';
@Component({
  selector: 'app-wishlist',
  templateUrl: './wishlist.component.html',
  styleUrls: ['./wishlist.component.css']
})
export class WishlistComponent implements OnInit {
  carts : Carts
  i:number=0
  j:number=0
  cart : Carts
  user:any
  books:Books
  constructor(private router: ActivatedRoute, private apiservice : ApiserviceService, private router1: Router) { }

  ngOnInit() {
    this.user = JSON.parse(sessionStorage.getItem('user'))
    this.apiservice.getWish(this.carts, this.user.id).subscribe((carts)=>{
                     this.carts=carts
                       for(var data in carts){
                         this.i+=carts[data].product.resellPrice
                         this.j+=carts[data].product.actualPrice
                                             }
                                                                           })
  }
  remove(id){
    this.i=0
    this.j=0
    this.apiservice.delWish(this.carts, id, this.user.id).subscribe((carts)=>{
              this.carts=carts
              alert("Successfully delete item from Wishlist...!!")
              for(var data in carts){
                this.i+=carts[data].product.resellPrice
                this.j+=carts[data].product.actualPrice
                                      }
                this.apiservice.getCartcount(this.user.id).subscribe((i)=>{
                  this.j=i.count
                document.getElementById("carttext").innerText= "Cart :"+this.j + " item(s)";
                this.apiservice.getWishcount(this.user.id).subscribe((i)=>{
                this.j=i.count
                document.getElementById("wishtext").innerText= "WishList :"+this.j + " item(s)";
                  })
                  })
              this.router1.navigateByUrl('/wish')
                                                                            })
  } 
  addCart(id){
    this.i=0
    this.j=0
    if(this.user == ''){
      alert("PLease SignUp / LogIn to Continue...!!")
      this.router1.navigateByUrl('/login')
    }else{
      this.apiservice.addCart(this.books,id,this.user.id).subscribe((books)=>{
        if(books.status == 1){
          this.apiservice.getCartcount(this.user.id).subscribe((i)=>{
            this.j=i.count
           document.getElementById("carttext").innerText= "Cart :"+this.j + " item(s)";
           this.apiservice.getWishcount(this.user.id).subscribe((i)=>{
            this.j=i.count
           document.getElementById("wishtext").innerText= "WishList :"+this.j + " item(s)";
            })
            this.apiservice.getWish(this.carts, this.user.id).subscribe((carts)=>{
              this.carts=carts
                for(var data in carts){
                  this.i+=carts[data].product.resellPrice
                  this.j+=carts[data].product.actualPrice
                                      }
                                      this.router1.navigateByUrl('/wish')
                                                                    })
            })
          alert("Item Succesfully Added to Cart")
          
        }else{
        alert("Item already added in cart...!!")
        
      }
        this.router1.navigateByUrl('/wish')
        })
      }
    }    
}

