import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, ParamMap,Router} from "@angular/router";
import { ApiserviceService } from '../apiservice.service'
import { Carts } from '../carts';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {
  selected: number;
  carts : Carts
  i:number=0
  j:number=0
  cart : Carts
  user:any
  constructor(private router: ActivatedRoute, private apiservice : ApiserviceService, private router1: Router) { }

  ngOnInit() {
       this.user = JSON.parse(sessionStorage.getItem('user'))
       this.apiservice.getCart(this.carts, this.user.id).subscribe((carts)=>{
                        this.carts=carts
                          for(var data in carts){
                            this.i+=carts[data].product.resellPrice
                            this.j+=carts[data].product.actualPrice
                                                }
                                                                              })
  }
  remove(id){
    this.i=0
    this.j=0
    this.apiservice.delCart(this.carts, id, this.user.id).subscribe((carts)=>{
              this.carts=carts
              for(var data in carts){
                this.i+=carts[data].product.resellPrice
                this.j+=carts[data].product.actualPrice
                                      }
                this.apiservice.getCartcount(this.user.id).subscribe((i)=>{
                  this.j=i.count
                  document.getElementById("carttext").innerText= "Cart :"+this.j + " item(s)";
                  this.apiservice.getWishcount(this.user.id).subscribe((i)=>{
                  this.j=i.count
                  document.getElementById("wishtext").innerText= "WishList :"+this.j + " item(s)";
                  })
                  })
              alert("Item Delete from Cart..!!")
              this.router1.navigateByUrl('/cart')
                                                                            })
  }
  checkout(){
    this.i=0
    this.j=0
    this.apiservice.checkOut(this.user.id).subscribe((carts)=>{
            this.router1.navigateByUrl('/')
                                                              })
            }     
}



