import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, ParamMap,Router} from "@angular/router";
import { ApiserviceService } from '../apiservice.service'
import { Books } from '../books'
import { Models } from '../models'
import { Messages } from '../messages'
@Component({
  selector: 'app-single-view',
  templateUrl: './single-view.component.html',
  styleUrls: ['./single-view.component.css'],
})
export class SingleViewComponent implements OnInit {
  selected: number;
  constructor(private router: ActivatedRoute, private apiservice : ApiserviceService, private router1: Router) { }
  books : Books 
  model : Models
  message : Messages
  query : string;
  j:number =0;
  user:any;
  ngOnInit() {
    if(JSON.parse(sessionStorage.getItem('user')) == null){
      this.user=''
    }else{
      this.user = JSON.parse(sessionStorage.getItem('user'))
    }
    this.router.paramMap.subscribe( (params: ParamMap) =>{
      this.selected = +(params.get('id'));
      this.apiservice.getBookId(this.books,this.selected).subscribe((books)=>{
        this.books=books[0]
        this.apiservice.getMessage(this.message,this.books.id).subscribe((message)=>{
                    this.message=message
          })

        })
    });
  }
addCart(id){
  if(this.user == ''){
    alert("PLease SignUp / LogIn to Continue...!!")
    this.router1.navigateByUrl('/login')
  }else{
    this.apiservice.addCart(this.books,id,this.user.id).subscribe((books)=>{
      if(books.status == 1){
        alert("Item Succesfully Added to Cart")
    
      }else{
      alert("Item already added in cart...!!")
      
    }
      this.router1.navigateByUrl('/')
      })
    }
  }
addWish(id){
  if(this.user == ''){
    alert("PLease SignUp / LogIn to Continue...!!")
    this.router1.navigateByUrl('/login')
  }else{
    this.apiservice.addWish(this.books,id,this.user.id).subscribe((books)=>{
      if(books.status == 1){
        alert("Item Succesfully Added to Wishlist..!!")
      }else{
      alert("Item already added in Wishlist / Cart...!!")
      
    }
      this.router1.navigateByUrl('/')
      })
    }
  }
  delBook(id){
    this.apiservice.deleteBook(this.books,id,this.user.id).subscribe((books)=>{
      this.router1.navigateByUrl('/')
      })
  }
  addMsg(pid,query){
    if(this.user == ''){
      alert("PLease SignUp / LogIn to Continue...!!")
      this.router1.navigateByUrl('/login')
    }else{
    if(query==null || query == ""){
        alert("Please type a query to Post...!!");
    }else{
    let message : Messages = {
      productkey:pid,
      text:query,
      postedby : this.user.id 
                              }
      this.query=''
      this.apiservice.addMessage(message).subscribe((message)=>{
      alert("message added successfully..!!")
      this.apiservice.getMessage(this.message,pid).subscribe((message)=>{
        this.message=message
                                                                        })
        
                                                              })
    

        }
    }
  }
}
