import { Component, OnInit } from '@angular/core';
import { Books } from '../books'
import { ApiserviceService } from '../apiservice.service'
import { ActivatedRoute, Router } from '@angular/router';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { routerNgProbeToken } from '@angular/router/src/router_module';

@Component({
  selector: 'app-sell',
  templateUrl: './sell.component.html',
  styleUrls: ['./sell.component.css']
})
export class SellComponent implements OnInit {
   user:any;

  constructor(private apiservice : ApiserviceService, private http: HttpClient) { }
  ngOnInit() {
    if(JSON.parse(sessionStorage.getItem('user')) == null){
    }else{
      this.user = JSON.parse(sessionStorage.getItem('user'))
    }
  }
selectedFile: File;
onFileSelected(event){
  this.selectedFile = event.target.files[0]
}


// onUpload() {
//   var fd = new FormData();
//   fd.append('image', this.selectedFile, this.selectedFile.name);
//   this.http.post('http://localhost:3333/upload', fd).subscribe(res=>{
//   })
    
// }

addUser(frm){
  var s=''
  var fd = new FormData();
  if(frm.value.authorName==''||frm.value.condition==""||frm.value.actualPrice==""||frm.value.resellPrice==""||this.user.id==""||this.selectedFile==undefined){
    alert("Please fill all the details of the form....!!!")
  }else{
    fd.append('image', this.selectedFile, this.selectedFile.name);
    this.http.post('/upload',fd).subscribe(res=>{
       s = res.toString()
       let book : Books = {
        name : frm.value.name,
        authorName : frm.value.authorName,
        condition : frm.value.condition,
        actualPrice : frm.value.actualPrice,
        resellPrice : frm.value.resellPrice, 
        image : 'uploads/'+s,
        postedby : this.user.id,
      }
      this.apiservice.addBook(book).subscribe(book =>{
        alert("Successfully Added Book for sale..!!")   
      })
      })
  }
}
}
