import { Component, OnInit } from '@angular/core';
import { ApiserviceService } from '../apiservice.service'
import { Models } from '../models'
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})

export class SignupComponent implements OnInit {

  constructor(private apiservice : ApiserviceService,private currentRoute: ActivatedRoute, private router: Router) { }
  ngOnInit() {
  }

  addUser(frm){
    if(frm.value.fname==""||frm.value.lname==''|| frm.value.address==''||frm.value.college==''||frm.value.course==""||frm.value.email==""||frm.value.mob==""||frm.value.pass==""){
      alert("Please fill all the details of the form....!!!")
      }else{
      let model : Models = {
        email : frm.value.email,
      password :frm.value.pass
      }
      this.apiservice.getUser(model).subscribe((model) =>{
      if(model==null){
        let model : Models = {
          fname : frm.value.fname,
          lname : frm.value.lname,
          address : frm.value.address,
          college : frm.value.college,
          course : frm.value.course, 
          email : frm.value.email,
          mobile : frm.value.mob,
        password :frm.value.pass
        }
        this.apiservice.addUser(model).subscribe(model =>{
          //this.getModels();
        })
        alert("Account created..Login to Continue...!!")
        this.router.navigateByUrl('/signup')
      }else{
        alert("EmailID already registered...!!")
      }})
    }
}
  logUser(frn){
    let model : Models = {
      email : frn.value.email,
    password :frn.value.password
    }
    this.apiservice.getUser(model).subscribe((model) =>{
      if(model==null){
        alert("EmailID is not registered..!!")
      }
      else if(model.password == frn.value.password && model.email == frn.value.email){
        sessionStorage.setItem('user' , JSON.stringify(model));

        alert("LogIn Successful...!!");
        this.router.navigateByUrl('/')
      }else{
        alert("Invalid EmailID OR Password..!!")
      }
    })
  }
}
