import { Component, OnInit } from '@angular/core';
import { ApiserviceService } from '../apiservice.service'
import { Books } from '../books'
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-body',
  templateUrl: './body.component.html',
  styleUrls: ['./body.component.css']
})
export class BodyComponent implements OnInit {
  fname:string
  books : Books
  user:any=''
  query:string;''
  pricesort:string;''
  constructor(private apiservice : ApiserviceService,private currentRoute: ActivatedRoute, private router: Router) { }
  
  ngOnInit() {
    if(JSON.parse(sessionStorage.getItem('user')) == null){
    }else{
      this.user = JSON.parse(sessionStorage.getItem('user'))
      this.fname=this.user.fname
    }
  this.apiservice.getBook(this.books).subscribe((books)=>{
    this.books=books
    })

    
  }
view(id){
    this.router.navigate(['view', id]);
}
search(query){
  this.apiservice.getBookByQuery(this.books,query).subscribe((books)=>{
    this.books=books
    })
}
sortPrice(query){
  this.apiservice.getMinMax().subscribe((books)=>{
    this.apiservice.getBookByPrice(this.books,query).subscribe((books)=>{
      this.books=books
      })
    })
}
reset(){
  this.query=''
  this.apiservice.getBook(this.books).subscribe((books)=>{
    this.books=books
    })
}
}
